import React from "react";
import classNames from "classnames";
import { SectionTilesProps } from "../../utils/SectionProps";
import SectionHeader from "./partials/SectionHeader";
import Image from "../elements/Image";

const propTypes = {
  ...SectionTilesProps.types,
};

const defaultProps = {
  ...SectionTilesProps.defaults,
};
const FeaturesTiles = ({
  className,
  topOuterDivider,
  bottomOuterDivider,
  topDivider,
  bottomDivider,
  hasBgColor,
  invertColor,
  pushLeft,
  ...props
}) => {
  const outerClasses = classNames(
    "features-tiles section",
    topOuterDivider && "has-top-divider",
    bottomOuterDivider && "has-bottom-divider",
    hasBgColor && "has-bg-color",
    invertColor && "invert-color",
    className
  );

  const innerClasses = classNames(
    "features-tiles-inner section-inner pt-0",
    topDivider && "has-top-divider",
    bottomDivider && "has-bottom-divider"
  );

  const tilesClasses = classNames(
    "tiles-wrap center-content",
    pushLeft && "push-left"
  );

  const sectionHeader = {
    title: "Come como te mereces",
    paragraph:
      "Eres lo que comes. Nuestros productos son saludables, organicos y beneficiaran tu salud de gran manera.",
  };

  return (
    <section {...props} className={outerClasses}>
      <div className="container">
        <div className={innerClasses}>
          <SectionHeader data={sectionHeader} className="center-content" />
          <div className={tilesClasses}>
            <div className="tiles-item reveal-from-bottom">
              <div className="tiles-item-inner">
                <div className="features-tiles-item-header">
                  <div className="mb-16">
                    <Image
                      src={require("./../../assets/images/melipona-honey.png")}
                      alt="Features tile icon 01"
                      width={150}
                      height={150}
                    />
                  </div>
                </div>
                <div className="features-tiles-item-content">
                  <h4 className="mt-0 mb-8">Miel de abejas meliponas</h4>
                  <p className="m-0 text-sm">
                    Creada por una abeja sin aguijón cuyo nombre en maya es
                    Xunán-Kab, y que solo habita en la Península de Yucatán. Es
                    la polinizadora por excelencia de las selvas tropicales de
                    esta zona de México. Su miel proviene de flores de las
                    selvas yucatecas.
                  </p>
                </div>
              </div>
            </div>

            <div
              className="tiles-item reveal-from-bottom"
              data-reveal-delay="200"
            >
              <div className="tiles-item-inner">
                <div className="features-tiles-item-header">
                  <div className="mb-16">
                    <img
                      src={require("./../../assets/images/granola.png")}
                      alt="Features tile icon 02"
                      width={150}
                      height={150}
                    />
                  </div>
                </div>
                <div className="features-tiles-item-content">
                  <h4 className="mt-0 mb-8">Granola artesanal</h4>
                  <p className="m-0 text-sm">
                    Nuestra granola es una mezcla de hojuelas de avena, frutos
                    secos y nueces. A esta mezcla se le puede agregar especias,
                    otros granos y algún endulzante. Normalmente, se mete al
                    horno con aceite vegetal, mantequilla o aceite de oliva a
                    dorar.
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

FeaturesTiles.propTypes = propTypes;
FeaturesTiles.defaultProps = defaultProps;

export default FeaturesTiles;
